<template>
  <!-- 輸入代辦項目 input -->
  <div class="position-relative input-group-lg mb-3">
    <font-awesome-icon class="position-absolute pull-left-icon text-secondary" icon="plus" size="lg" fixed-width />
    <input @keyup.enter="submit" type="text" class="form-control icon-input" placeholder="新增待辦項目..."
      v-model="todo.name">
  </div>
</template>

<script>
import Todo from '@/model/Todo'
import { rtdbDB, getUserDBRef } from '@/service/rtdb_db'

export default {
  data () {
    const todo = new Todo()
    return {
      todo
    }
  },
  computed: {
    todoGroupUrl () {
      if (this.$route.path !== '/') {
        return this.$route.params.group
      } else {
        return this.todo.group
      }
    }
  },
  methods: {
    submit () {
      this.todo.group = this.todoGroupUrl
      rtdbDB.ref(`${getUserDBRef()}/todoList/${this.todoGroupUrl}`).push(this.todo)
      this.$messageHandle('success', `${this.todo.name} 新增成功！`)

      this.todo.name = ''
    }
  }
}
</script>

<style>
</style>



