<template>
  <div>
    <todo-add />
    <div v-if="isFirst" class="first-text animated infinite pulse slow">
      <font-awesome-icon class="fa-rotate-180" icon="level-down-alt" size="lg" fixed-width />
      <p class="todo-first-text">從這邊快速新增，你的第一筆代辦事項</p>
    </div>

    <!-- 代辦list -->
    <div v-else class="todolist-wrap">
      <half-circle-spinner v-if="!isLoading" :animation-duration="1000" :size="40" :color="'#007bff'" />
      <template v-else v-for="(item, key) in todoGroupList">
        <NTodoGroupRow :todos='item' :ID='key' :key='key' v-if="Object.keys(item).length > 1" />
      </template>
    </div>

    <!-- 新增按鈕 -->
    <div class="fixed-bottom text-center mb-4">
      <router-link :to="{path: `/todoedit/new/0`}" class="text-primary ">
        <font-awesome-icon icon="plus-circle" style="font-size: 3rem;" />
      </router-link>
    </div>
  </div>
</template>

<script>
import NTodoGroupRow from '@/components/template/n-todo-group-row.vue'
import TodoAdd from '@/components/template/todo-add'

import { rtdbDB, getUserDBRef } from '@/service/rtdb_db'

export default {
  name: 'todoList',
  components: {
    NTodoGroupRow,
    TodoAdd
  },
  firebase: {
    db: rtdbDB.ref('dessert1'),
    root: rtdbDB.ref('/')
  },
  data () {
    return {
      todoGroupList: {},
      isFirst: true
    }
  },
  watch: {
    'todoGroupList': {
      deep: true,
      handler (newValue, oldValue) {
        const todoGroup = Object.keys(this.todoGroupList).length === 1
        const todoGroupCount = Object.keys(this.todoGroupList.today).length === 1
        this.isFirst = todoGroup && todoGroupCount
      }
    }
  },
  created () {
    rtdbDB.ref(`${getUserDBRef()}/todoList`).on('value', snapshot => {
      const data = snapshot.val()
      const dict = Object.assign({}, this.todo)
      dict.name = 'default'
      dict.isShow = false

      const init = () => new Promise((resolve, reject) => {
        resolve(data)
      })

      init()
        .then(data => {
          if (data) {
          // 有，把DB資料全塞到畫面
            this.todoGroupList = data
          } else {
            // 沒有，就先新增一個today分類到DB
            rtdbDB.ref(`${getUserDBRef()}/todoList/today`).push(dict)
          }
        })
        .catch(err => {
          this.$swalErrorHandle(err.message)
        })
        .finally(() => {
          this.isLoading = true
        })
    })
  }
}
</script>



