<template>
  <div>
    <router-link :to="{path: `/nTodoGroupList/${ID}`}">
      <button class="btn w-100 text-left p-0 d-flex align-items-center" v-if="!allIsCheck" type="button">
        <label for="dateline" class="col-form-label">
          <font-awesome-icon fixed-width class="ml-auto text-dark" size="lg" icon="folder-open" />
          {{ID}}
        </label>
        <span v-if="todoTurnArray.length > 1" :class="{'ml-auto': todoTurnArray.length > 1}" class="ml-auto badge badge-primary">{{todoTurnArray.length-1}}</span>
      </button>
    </router-link>
    <font-awesome-icon :class="{'ml-auto': todoTurnArray.length === 1}" @click.stop="remove" class="ml-3 trash" size="lg" icon="trash" />
    <hr>
  </div>
</template>

<script>
import { rtdbDB, getUserDBRef } from '@/service/rtdb_db'

export default {
  props: {
    todos: {
      type: Object,
      required: true
    },
    ID: {
      type: String,
      required: true
    }
  },
  data () {
    return {
      isImportant: false,
      isCheck: false
    }
  },
  computed: {
    allIsCheck () {
      const isCheckTodos = this.todoTurnArray.filter(item => item.isCheck === true)
      return isCheckTodos.length === this.todoTurnArray.length
    },
    todoTurnArray () {
      return Object.keys(this.todos).map(item => {
        return this.todos[item]
      })
    }
  },
  methods: {
    remove () {
      this.$swal.fire({
        title: `確定刪除${this.ID}資料夾`,
        text: '刪除後將不可修復',
        type: 'warning',
        showCancelButton: true,
        confirmButtonColor: '#3085d6',
        cancelButtonColor: '#d33',
        confirmButtonText: '確定',
        cancelButtonText: '取消'
      }).then((result) => {
        if (result.value) {
          rtdbDB.ref(`${getUserDBRef()}/todoList/${this.ID}`).remove()
          this.$swalSuccessHandle('刪除成功')
        }
      })
    }
  }
}
</script>

<style scoped>
a {
  display: inline-block;
  width: calc(100% - 50px)
}
.trash {
  cursor: pointer;
}
</style>



